/******************************************
 *         components/product-aside
 =========================================*/
.product-filter {
  display: block;
  box-shadow: 0 0 0.2rem rgba(0, 0, 0, 0.2); }

.product-filter--title {
  padding: 0.75rem;
  display: block;
  font-size: 1rem;
  background-color: #F8F8F8;
  color: #666;
  font-weight: 500;
  font-size: 120%; }

.product-filter--block {
  position: relative;
  padding: 1.25rem;
  font-size: 0.875rem;
  border: 1px solid #CCC; }

.product-filter--block--title {
  display: inline-block;
  margin-bottom: 1.25rem;
  font-weight: bold; }
  .product-filter--block--title--link {
    border-bottom: 1px dashed #666;
    color: #666; }
    .product-filter--block--title--link:hover {
      color: black; }

.product-filter--block--data {
  position: absolute;
  right: 0;
  display: inline-block;
  padding: 0.2rem 0.4rem;
  color: #CCC;
  cursor: pointer;
  border: 1px dashed #CCC;
  border-right: none;
  border-radius: 2px; }
  .product-filter--block--data:hover::after {
    position: absolute;
    top: 120%;
    left: 0;
    box-shadow: 0 0 0.4rem rgba(0, 0, 0, 0.2);
    color: black;
    padding: 0.625rem;
    z-index: 999;
    content: attr(data-title);
    background-color: #F8F8F8;
    border-radius: 4px; }

.product-filter--block--list {
  display: block;
  line-height: 2; }
  .product-filter--block--list::after {
    clear: both;
    content: "";
    display: table; }
  .product-filter--block--list li {
    float: left;
    display: block;
    width: 50%; }
  .product-filter--block--list.hide {
    display: none; }

.product-filter--block--link {
  border-bottom: 1px dashed #CCC; }

.product-filter--block--input {
  margin-bottom: 1.25rem;
  text-align: center;
  line-height: 1.5rem; }
  .product-filter--block--input::after {
    clear: both;
    content: "";
    display: table; }

.product-filter--block--input input {
  display: inline-block;
  width: 45%;
  padding: 0.25rem;
  border: 1px solid #CCC; }
  .product-filter--block--input input:first-child {
    float: left; }
  .product-filter--block--input input:last-child {
    float: right; }
  .product-filter--block--input input:focus {
    border-color: #165E8A;
    box-shadow: 0 0 0.2rem rgba(22, 94, 138, 0.6); }

.product-filter--block--range input {
  width: 100%;
  margin: 0;
  background: transparent;
  border: 4px solid transparent;
  color: transparent; }

.product-filter--block--title--link.open {
  color: #000; }

.product-filter--block--title--link.open:before {
  margin-right: 0.25rem;
  line-height: 80%;
  color: inherit;
  font-family: 'icon';
  font-size: 50%;
  content: 'd'; }

.product-filter--block--title--link.close:before {
  margin-right: 0.25rem;
  line-height: 80%;
  color: inherit;
  font-family: 'icon';
  font-size: 50%;
  content: 'f'; }
